import { IFranchise } from '@interfaces';
import { BusinessPayload } from '@payloads';
import { FranchiseService } from '@services';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Component, OnInit, OnDestroy } from '@angular/core';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControl,
} from '@angular/forms';
import {
  Style,
  PriceRange,
  ResponsePriceRanges,
  ResponseStyles,
  Franchise,
} from '@models';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-business',
  templateUrl: './business.component.html',
  styleUrls: ['./business.component.scss'],
})
export class BusinessComponent implements OnInit, OnDestroy {
  public businessForm: FormGroup = this.fb.group({
    id: new FormControl(0),
    name: new FormControl('', [
      Validators.required,
      Validators.minLength(3),
      Validators.maxLength(150),
    ]),
    style: new FormControl('', Validators.required),
    price_range: new FormControl('', Validators.required),
  });
  public franchise: Franchise = Franchise.emptyObject();
  public styleList: Array<Style> = [];
  public priceRangesList: Array<PriceRange> = [];
  private currentFranchiseSubscription: Subscription = Subscription.EMPTY;
  private priceRangesSubscription: Subscription = Subscription.EMPTY;
  private stylesSubscription: Subscription = Subscription.EMPTY;
  private franchiseSubscription: Subscription = Subscription.EMPTY;

  constructor(
    private fb: FormBuilder,
    private franchiseService: FranchiseService,
    private dialog: MatSnackBar
  ) {}

  ngOnInit() {
    this.getStyles();
    this.getPriceRanges();
    this.getFranchise();
  }

  ngOnDestroy() {
    this.currentFranchiseSubscription.unsubscribe();
    this.priceRangesSubscription.unsubscribe();
    this.stylesSubscription.unsubscribe();
    this.franchiseSubscription.unsubscribe();
  }

  /**
   * Sends PUT request to update business information.
   * Updates Franchise information saved in local storage with request response
   */
  public onSubmit(): void {
    const payload = new BusinessPayload(this.businessForm);

    this.franchiseSubscription = this.franchiseService
      .updateFranchise(this.franchise.id, payload.toJson())
      .subscribe((response: IFranchise) => {
        const updatedFranchise = new Franchise(response);
        this.franchiseService.setFranchise(updatedFranchise);
        this.showToast('La información de actualizó con exito');
      });
  }

  /**
   * Gets Franchise information saved in localstorage
   */
  private getFranchise(): void {
    this.currentFranchiseSubscription = this.franchiseService.currentFranchise.subscribe(
      (franchise: Franchise) => {
        this.franchise = franchise;
        this.patchForm();
      }
    );
  }

  /**
   * Sends GET request to obtain price range options.
   */
  private getPriceRanges(): void {
    this.priceRangesSubscription = this.franchiseService
      .getPriceRanges()
      .subscribe((response: ResponsePriceRanges) => {
        this.priceRangesList = response.price_ranges;
      });
  }

  /**
   * Sends GET request to obtain restaurant style options.
   */
  private getStyles(): void {
    this.stylesSubscription = this.franchiseService
      .getStyles()
      .subscribe((response: ResponseStyles) => {
        this.styleList = response.styles;
      });
  }

  /**
   * Patches form with franchise information
   */
  private patchForm(): void {
    if (this.franchise) {
      this.businessForm.patchValue({
        id: this.franchise.id || null,
        name: this.franchise.name,
        style: this.franchise.style,
        price_range: this.franchise.price_range,
      });
    }
  }

  /**
   * Emits Toast notification event
   * @params Message string to show on Toast notification
   */
  private showToast(successMessage: string): void {
    this.dialog.open(successMessage, 'Entendido', {
      duration: 15000,
    });
  }
}
